import { Button, Form, Input, message, Modal, Spin } from 'antd';
import { useContext, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { axiosClient } from '../../api/config';
import { AuthContext } from '../../contexts/AuthProvider';
import { removeCookies } from '../../utils/Cookies';
import { LoadingOutlined } from '@ant-design/icons';

const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;

export const UserOption = (props) => {
  const authCt = useContext(AuthContext);
  const history = useHistory();
  const [username, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [isLoadingUpdatePassword, setLoadingUpdatePassword] = useState(false);

  const handleLogout = () => {
    removeCookies();
    authCt.setAuth({ name: '', role: '' });
    message.success('ng xut thnh cng');
    setTimeout(() => {
      history.push('/');
    }, 1000);
  };

  const handleOk = () => {
    setLoadingUpdatePassword(true);
    axiosClient
      .patch('/users', {
        username,
        oldPassword: password,
        newPassword,
      })
      .then((res) => {
        setLoadingUpdatePassword(false);
        props.setIsModalVisible(false);
        message.success('i mt khu thnh cng mi ng nhp li');
        setTimeout(() => {
          window.location.reload();
        }, 1000);
      })
      .catch((err) => {
        setLoadingUpdatePassword(false);
        if (err.response.status === 400) {
          window.location.reload();
        } else {
          message.error(err.response.data.message);
        }
      });
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      <Button onClick={props.showModal} style={{ border: 'none' }}>
        i mt khu
      </Button>
      <Modal
        visible={props.isModalVisible}
        onCancel={() => props.setIsModalVisible(false)}
        title="i mt khu"
        destroyOnClose={true}
        footer={[
          <Button
            key="back"
            onClick={props.handleCancel}
            style={{ borderRadius: 5 }}
          >
            Hy
          </Button>,
          <Button
            type="primary"
            onClick={handleOk}
            style={{
              backgroundColor: 'rgb(76, 124, 253)',
              color: '#fff',
              borderRadius: 5,
            }}
          >
            Cp nht
          </Button>,
        ]}
      >
        <Form layout="vertical" name="form_in_modal">
          <Form.Item
            label="Tn ng nhp"
            name="username"
            rules={[{ required: true, message: 'Nhp tn ng nhp!' }]}
          >
            <Input
              onChange={(e) => {
                setUserName(e.target.value);
              }}
              allowClear={true}
            />
          </Form.Item>
          <Form.Item
            name="pass"
            label="Mt khu hin ti"
            rules={[
              {
                required: true,
                message: 'Mt khu khng c b trng!',
              },
            ]}
          >
            <Input.Password
              onChange={(e) => setPassword(e.target.value)}
              allowClear={true}
            />
          </Form.Item>
          <Form.Item
            name="confirm"
            label="Mt khu mi"
            rules={[
              {
                required: true,
                message: 'Mt khu khng c b trng!',
              },
            ]}
          >
            <Input.Password
              onChange={(e) => setNewPassword(e.target.value)}
              allowClear={true}
            />
          </Form.Item>
        </Form>

        {isLoadingUpdatePassword ? (
          <Spin indicator={antIcon} style={{ width: '100%' }} />
        ) : (
          <></>
        )}
      </Modal>
      <Button style={{ border: 'none' }} onClick={handleLogout}>
        ng xut
      </Button>
    </div>
  );
};
