import { Button } from 'antd';
import { FileExcelFilled } from '@ant-design/icons';
import { useEffect, useState } from 'react';
import { axiosClient } from '../../../../api';
import { CSVLink } from 'react-csv';
import moment from 'moment';

const headers = [
  { label: 'STT', key: 'stt' },
  { label: 'H v tn', key: 'name' },
  { label: 'M hc vin', key: 'msv' },
  { label: 'Chuyn ', key: 'category' },
  { label: 'Thi gian vo', key: 'timeIn' },
  { label: 'Thi gian ra', key: 'timeOut' },
  { label: 'Lp', key: 'class' },
  { label: 'Ngy', key: 'date' },
  { label: 'Trng thi', key: 'status' },
];

const ExportFile = (props) => {
  const [data, setData] = useState([]);

  const csvReport = {
    filename: `im danh ${moment(props.dateAttendence).format(
      'DD-MM-YYYY'
    )} Lp ${props.classObject.name}.xlsx`,
    headers,
    data,
  };

  const hanleGetDate = () => {
    axiosClient
      .get(
        `/attendence?date=${props.dateAttendence}&semesterId=${props.semester?.id}&classId=${props.classObject?.id}`
      )
      .then((res) => {
        const mappingDate = res.data.data.map((item, index) => ({
          stt: index + 1,
          name: item.name,
          msv: item.msv,
          category: item.category,
          timeIn: moment(item.timeIn).format('HH:MM:SS'),
          timeOut: moment(item.timeOut).format('HH:MM:SS'),
          class: props.classObject.name,
          date: moment(new Date(item.date)).format('DD-MM-YYYY'),
          status:
            item.status === 'attend'
              ? 'C mt'
              : item.status === 'absent'
              ? 'Vng'
              : 'Mun',
        }));

        setData(mappingDate);
      });
  };

  useEffect(() => {
    hanleGetDate();
  }, [props.dateAttendence]);

  return (
    <CSVLink {...csvReport}>
      <Button
        icon={<FileExcelFilled style={{ color: '#366F38' }} />}
        size="large"
        style={{ borderRadius: 5 }}
      >
        Xut danh sch
      </Button>
    </CSVLink>
  );
};

export default ExportFile;
